.admin-wrapper {
  display: flex;
  flex-flow: nowrap row;
  justify-content: flex-start;
  align-items: flex-start;
}
.admin-wrapper .nav {
  height: 100vh;
  width: 10vw;
  min-width: 180px;
  background-color: #001529;
}
.admin-wrapper .main-wrapper {
  height: 100vh;
  width: 90vw;
  max-width: calc(100vw-180px);
  background-color: #F1F3F4;
}
.admin-wrapper .main-wrapper .content {
  padding: 24px;
  margin: 24px 16px;
  background-color: #fff;
  min-height: 280px;
}
.admin-wrapper .footer {
  text-align: center;
}
