.header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 3px 3px 0 #F1F3F4;
  background-color: #fff;
  padding: 0 24px;
}
.header .trigger:hover {
  color: #1890ff;
}
.header .user {
  margin-right: 10px;
}
.header .user:hover {
  color: #1890ff;
}
.ant-menu {
  border: none;
}
